.weather {
    width:300px;
    max-width: 100%;
    cursor: pointer;
    text-align: center;
    margin: 0 auto 30px;
    background: linear-gradient(rgb(0, 0, 0), transparent) rgb(51, 51, 51);
    color: #fff;
    padding: 10px;
    border-radius: 10px;


}

.weatherImage {
    cursor: pointer;
}

.icon {
    width:50px;
    background: #fff;
    border-radius: 50%;
    margin: 0 auto;
    object-fit: cover;
    position: relative;
    bottom: 30px;


}

.icon img {
    width:100%;
    height:auto;
    object-fit: cover;
}

.weatherDesc {
    cursor: pointer;
}

